<template>
  <div class="app">
    <el-row>
      <el-col :span="10" class="el-colStyle el-col-hight">
        <el-tabs>
          <el-tab-pane label="购物车">
            <el-table :data="tableData" border stripe>
              <el-table-column prop="goodsName" label="商品"></el-table-column>
              <el-table-column prop="count" label="数量"></el-table-column>
              <el-table-column prop="price" label="价格"></el-table-column>
              <el-table-column label="操作" fixed="right">
                <template scope="scope">
                  <!--没有template不行-->
                  <!-- 通过 Scoped slot 可以获取到 row, column,
                                                 $index 和 store（table 内部的状态管理）的数据，
                                                 用法参考 demo。(1.1 后支持通过 Scoped slot 自定义模板。
                                                之前的 inline-template 同样适用，但不推荐。) -->
                  <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                  <el-button type='text' size="small" @click="addOrderList(scope.row)">添加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div style="margin-top:10px">
              <small>总数量: </small>{{totalCount}}
              <small style="margin-left: 20px">总价格: </small>￥{{totalMoney}}元
            </div>
            <div style="margin-top:50px">
              <el-button :plain="true" type="success" style="margin-right: 30px" @click="ok">下单</el-button>
              <el-button :plain="true" type="danger">撤销</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">敬请期待！</el-tab-pane>
          <el-tab-pane label="外卖">敬请期待！</el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="14" class="el-col-hight">
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="food in oftenGoods" :key="food.goodsId" @click="addOrderList(food)">
                <span>{{food.goodsName}}</span>
                <span class="o-price">￥{{food.price}}元</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
          <el-tabs type="card">
            <el-tab-pane label="汉堡">
              <ul class='cookList'>
                <li v-for="tygood in type0Goods" :key="tygood.goodsId" @click="addOrderList(tygood)">
                  <span class="foodImg">
                    <img :src="tygood.goodsImg" width="100%">
                  </span>
                  <span class="foodName">{{tygood.goodsName}}</span>
                  <span class="foodPrice">￥{{tygood.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <ul class='cookList'>
                <li v-for="tygood in type1Goods" :key="tygood.goodsId" @click="addOrderList(tygood)">
                  <span class="foodImg">
                    <img :src="tygood.goodsImg" width="100%">
                  </span>
                  <span class="foodName">{{tygood.goodsName}}</span>
                  <span class="foodPrice">￥{{tygood.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <ul class='cookList'>
                <li v-for="tygood in type2Goods" :key="tygood.goodsId" @click="addOrderList(tygood)">
                  <span class="foodImg">
                    <img :src="tygood.goodsImg" width="100%">
                  </span>
                  <span class="foodName">{{tygood.goodsName}}</span>
                  <span class="foodPrice">￥{{tygood.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <ul class='cookList'>
                <li v-for="tygood in type3Goods" :key="tygood.goodsId" @click="addOrderList(tygood)">
                  <span class="foodImg">
                    <img :src="tygood.goodsImg" width="100%">
                  </span>
                  <span class="foodName">{{tygood.goodsName}}</span>
                  <span class="foodPrice">￥{{tygood.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'app',
  data() {
    return {
      tableData: [],
      oftenGoods: [
        {
          goodsId: 1,
          goodsName: '香辣鸡腿堡',
          price: 18
        }, {
          goodsId: 2,
          goodsName: '田园鸡腿堡',
          price: 15
        }, {
          goodsId: 3,
          goodsName: '和风汉堡',
          price: 15
        }, {
          goodsId: 4,
          goodsName: '快乐全家桶',
          price: 80
        }, {
          goodsId: 5,
          goodsName: '脆皮炸鸡腿',
          price: 10
        }, {
          goodsId: 6,
          goodsName: '魔法鸡块',
          price: 20
        }, {
          goodsId: 7,
          goodsName: '可乐大杯',
          price: 10
        }, {
          goodsId: 8,
          goodsName: '雪顶咖啡',
          price: 18
        }, {
          goodsId: 9,
          goodsName: '大块鸡米花',
          price: 15
        }, {
          goodsId: 20,
          goodsName: '香脆鸡柳',
          price: 17
        }

      ],
      type0Goods: [
        {
          goodsId: 1,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
          goodsName: '香辣鸡腿堡',
          price: 18
        }, {
          goodsId: 2,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
          goodsName: '田园鸡腿堡',
          price: 15
        }, {
          goodsId: 3,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
          goodsName: '和风汉堡',
          price: 15
        }],
      type1Goods: [{
        goodsId: 4,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
        goodsName: '快乐全家桶',
        price: 80
      }, {
        goodsId: 5,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
        goodsName: '脆皮炸鸡腿',
        price: 10
      }, {
        goodsId: 6,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
        goodsName: '魔法鸡块',
        price: 20
      }],
      type2Goods: [{
        goodsId: 7,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
        goodsName: '可乐大杯',
        price: 10
      }, {
        goodsId: 8,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
        goodsName: '雪顶咖啡',
        price: 18
      }],
      type3Goods: [{
        goodsId: 9,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
        goodsName: '大块鸡米花',
        price: 15
      }, {
        goodsId: 20,
        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
        goodsName: '香脆鸡柳',
        price: 17
      }
      ],
      totalCount: 0,
      totalMoney: 0,
    }
  },
  mounted: function () {
    var clientHight = document.body.clientHeight;
    document.getElementsByClassName('el-col-hight').style.height = clientHight + "px";
  },
  methods: {
    delSingleGoods(goods) {
      console.log(goods);
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    ok: function () {
      if (this.tableData != 0) {
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message: "下单成功",
          type: 'success'
        })
      } else {
        this.$message.error('别急，我懂你的心情+_+')
      }
    },
    getAllMoney() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
        this.tableData.forEach((element) => {
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + (element.price * element.count);
        })
      }
    },
    //添加订单列表的方法
    addOrderList(goods) {
      this.totalCount = 0; //汇总数量清0
      this.totalMoney = 0;
      let isHave = false;
      //判断是否这个商品已经存在于订单列表
      for (let i = 0; i < this.tableData.length; i++) {
        console.log(this.tableData[i].goodsId);
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true; //存在
        }
      }
      //根据isHave的值判断订单列表中是否已经有此商品
      if (isHave) {
        //存在就进行数量添加
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        arr[0].count++; //这个arr是tableDta给过来的
        //console.log(typeof(arr));
      } else {
        //不存在就推入数组
        let newGoods = { goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price, count: 1 };
        this.tableData.push(newGoods);

      }
      this.getAllMoney();

    }
  }
}
</script>
<style>
.el-colStyle {
  background-color: #fefeff;
  border-right: 2px solid #f1f1f1;
}

.title {
  height: 20px;
  border-bottom: 1px solid #D3DCE6;
  background-color: #F9FAFC;
  padding: 10px;
}

.often-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #E5E9F2;
  padding: 10px;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}

.o-price {
  color: #58B7FF;
}

.goods-type {
  clear: both;
}

.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #E5E9F2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}

.cookList li span {

  display: block;
  float: left;
}

.foodImg {
  width: 40%;
}

.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}

.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
</style>



